<template>
  <div class="answering-box">
    <img class="banner" :src="`${publicPath}image/index_1.png`" alt="">
    <div class="content-box" :style="{'background-image': `url('${publicPath}image/answering_1.png')`}">
      <div class="tip">Please answer the following questions to complete the registration</div>
      <div v-if="content.length > 0" class="answering-content">
        <h1>
          Q{{active+1}}.{{ content[active].name }}?
        </h1>
        <van-radio-group v-model="content[active].user_anwser" class="radio-box">
          <van-radio v-for="answer in content[active].answers" :key="answer.id" :name="answer.value">{{answer.label}}.{{answer.name}}</van-radio>
        </van-radio-group>
        <ul>
          <li v-for="(item, i) in content" :key="i" :class="{active: active === i }" @click="active=i"> {{ i + 1 }} </li>
        </ul>
        <div class="btn-box">
          <div class="btn t" @click="goBack">Back</div>
          <div class="btn" @click="subimit">Submit</div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { preTestList, answerQuestion } from '@/api'
import { map } from 'underscore'
import { Toast } from 'vant'
export default {
  props: {
    id: {
      type: [String, Number],
      default: () => ''
    }
  },
  data() {
    return {
      publicPath: process.env.BASE_URL,
      radio: null,
      active: 0,
      content: []
    }
  },
  watch: {
    id: {
      handler() {
        this.getData()
      },
      immediate: true
    }
  },
  methods: {
    getData() {
      let data = {
        unit_id: this.id
      }
      if (this.id === 'test') {
        data = {}
      }
      preTestList(data).then(res => {
        this.content = map(res, item => {
          return {
            ...item,
            user_anwser: null
          }
        })
      }).catch(() => {})
    },
    goBack() {
      if (this.id === 'test') {
        this.$router.push({ path: '/' })
        return
      }
      this.$router.go(-1)
    },
    subimit() {
      for (const active in this.content) {
        if (!this.content[active].user_anwser) {
          this.active = Number(active)
          Toast('Please answer the questions！')
          return
        }
      }
      const data = {
        answers: map(this.content, item => {
          return {
            correct_answer: item.correct_answer,
            answer: item.user_anwser,
            type: item.type,
            question_id: item.id
          }
        })
      }
      answerQuestion(data).then(() => {
        Toast({
          type: 'success',
          message: 'submit is successed'
        })
        this.$router.push({ path: '/' })
      }).catch(() => {})
    }
  }
}
</script>
